// Initialize i18next
export async function initI18n() {
    await i18next
        .use(LanguageDetector)
        .init({
            resources,
            fallbackLng: 'en',
            debug: false,
            interpolation: {
                escapeValue: false // not needed for HTML
            }
        });
    
    return i18next;
}

// Translate all elements with translation keys
export function translate() {
    // App title
    document.getElementById('app-title').textContent = i18next.t('appTitle');
    
    // Section titles
    document.getElementById('input-title').textContent = i18next.t('inputTitle');
    document.getElementById('config-title').textContent = i18next.t('configTitle');
    document.getElementById('layout-title').textContent = i18next.t('layoutTitle');
    document.getElementById('visual-title').textContent = i18next.t('visualTitle');
    document.getElementById('export-title').textContent = i18next.t('exportTitle');
    document.getElementById('vis-title').textContent = i18next.t('visTitle');
    
    // Labels
    document.querySelector('label[for="language-select"]').textContent = i18next.t('language');
    document.getElementById('file-label').textContent = i18next.t('fileLabel');
    document.getElementById('load-sample').textContent = i18next.t('loadSample');
    
    // Configuration options
    document.querySelector('label[for="orientation"]').textContent = i18next.t('orientation');
    document.querySelector('label[for="node-size"]').textContent = i18next.t('nodeSize');
    document.querySelector('label[for="node-spacing"]').textContent = i18next.t('nodeSpacing');
    document.querySelector('label[for="color-scheme"]').textContent = i18next.t('colorScheme');
    document.querySelector('label[for="font-size"]').textContent = i18next.t('fontSize');
    document.querySelector('label[for="line-style"]').textContent = i18next.t('lineStyle');
    document.querySelector('label[for="svg-width"]').textContent = i18next.t('svgWidth');
    document.querySelector('label[for="svg-height"]').textContent = i18next.t('svgHeight');
    
    // Orientation options
    const orientationSelect = document.getElementById('orientation');
    orientationSelect.options[0].textContent = i18next.t('topDown');
    orientationSelect.options[1].textContent = i18next.t('bottomUp');
    orientationSelect.options[2].textContent = i18next.t('leftRight');
    orientationSelect.options[3].textContent = i18next.t('rightLeft');
    
    // Color scheme options
    const colorSchemeSelect = document.getElementById('color-scheme');
    colorSchemeSelect.options[0].textContent = i18next.t('default');
    colorSchemeSelect.options[1].textContent = i18next.t('genderBased');
    colorSchemeSelect.options[2].textContent = i18next.t('generationBased');
    colorSchemeSelect.options[3].textContent = i18next.t('custom');
    
    // Line style options
    const lineStyleSelect = document.getElementById('line-style');
    lineStyleSelect.options[0].textContent = i18next.t('straight');
    lineStyleSelect.options[1].textContent = i18next.t('curved');
    lineStyleSelect.options[2].textContent = i18next.t('angled');
    
    // Buttons
    document.getElementById('generate-tree').textContent = i18next.t('generateTree');
    document.getElementById('export-svg').textContent = i18next.t('exportSVG');
    document.getElementById('reset-view').textContent = i18next.t('resetView');
    
    // Footer
    document.querySelector('footer p').textContent = i18next.t('footerText');
}

// Get translation for a key
export function t(key, options = {}) {
    return i18next.t(key, options);
}

// Get current language
export function getCurrentLanguage() {
    return i18next.language;
}

// Change language
export function changeLanguage(lang) {
    return i18next.changeLanguage(lang);
}

export default {
    initI18n,
    translate,
    t,
    getCurrentLanguage,
    changeLanguage
}; 